<template>
    <div class="main">
        <el-page-header @back="goBack" :content="nmd.name"></el-page-header>
        <el-tabs v-model="activeName">
            <el-tab-pane label="管理员" name="first">
                <div class="select">
                    <el-input v-model="gid" placeholder="输入管理员账号" clearable></el-input>
                    <el-button type="primary" @click="submit">添加</el-button>
                </div>
                <el-table
                    border
                    tooltip-effect="dark"
                    style="width: 100%"
                    :header-cell-style="{background:'#f5f7fa'}"
                    :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
                >
                    <el-table-column prop="gid" label="账号"></el-table-column>
                    <el-table-column prop="name" label="姓名"></el-table-column>
                    <el-table-column align="right">
                        <template slot="header">
                            <el-input v-model="search" size="mini" placeholder="输入姓名搜索" />
                        </template>
                        <template slot-scope="scope">
                            <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.$index, scope.row)"
                            >移除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
            <el-tab-pane label="权限" name="second">
                <el-form ref="form" :model="form" label-width="90px">
                    <el-divider content-position="left">页面权限</el-divider>
                    <el-form-item label="人员管理">
                        <el-checkbox-group v-model="form.person">
                            <el-checkbox label="增" name="person"></el-checkbox>
                            <el-checkbox label="删" name="person"></el-checkbox>
                            <el-checkbox label="改" name="person"></el-checkbox>
                            <el-checkbox label="查" name="person"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="日志管理">
                        <el-checkbox-group v-model="form.log">
                            <el-checkbox label="增" name="log" disabled></el-checkbox>
                            <el-checkbox label="删" name="log" disabled></el-checkbox>
                            <el-checkbox label="改" name="log" disabled></el-checkbox>
                            <el-checkbox label="查" name="log"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="管理员列表">
                        <el-checkbox-group v-model="form.manager">
                            <el-checkbox label="增" name="manager" disabled></el-checkbox>
                            <el-checkbox label="删" name="manager" disabled></el-checkbox>
                            <el-checkbox label="改" name="manager"></el-checkbox>
                            <el-checkbox label="查" name="manager"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="角色管理">
                        <el-checkbox-group v-model="form.role">
                            <el-checkbox label="增" name="role"></el-checkbox>
                            <el-checkbox label="删" name="role"></el-checkbox>
                            <el-checkbox label="改" name="role"></el-checkbox>
                            <el-checkbox label="查" name="role"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-divider content-position="left">部门权限</el-divider>
                    <el-form-item label="部门">
                        <el-checkbox-group v-model="form.depPower">
                            <el-checkbox  v-for="d in departmentList" :key="d.value" :label="d.label" name="depPower"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <div style="text-align: left;">
                        <el-button type="primary" @click="onSubmit">提交</el-button>
                        <el-button @click="reset">重置</el-button>
                    </div>
                </el-form>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import axios from "axios";
export default {
    data() {
        return {
            activeName: "first",
            gid: "",
            nmd: {},
            search: "",
            departmentList: [],
            tableData: [
                {
                    gid: "12345",
                    name: "asdasd"
                },
                {
                    gid: "36925",
                    name: "sdfds"
                },
                {
                    gid: "98563",
                    name: "asfghd"
                },
                {
                    gid: "25631",
                    name: "ui"
                },
                {
                    gid: "65412",
                    name: "tryu"
                }
            ],
            form: {
                rid: "",
                name: "",
                person: [],
                log: [],
                manager: [],
                role: [],
                depPower: []
            },
            qxForm: {
                rid: "",
                name: "",
                pagePower: "",
                depPower: ""
            }
        };
    },
    created: function() {
        this.init();
        this.getList();
    },
    methods: {
        //初始化
        init() {
            this.nmd = this.$route.params.data;
            this.departmentList = this.$route.params.departmentList;
            this.form.rid = this.nmd.rid;
            this.form.name = this.nmd.name;
            let pagePower = parseInt(this.nmd.pagePower, 16).toString(2);
            pagePower = pagePower.split('').reverse().join('');
            //console.log(this.nmd.pagePower, pagePower);
            if (pagePower[0] === "1") this.form.person.push("查");
            if (pagePower[1] === "1") this.form.person.push("改");
            if (pagePower[2] === "1") this.form.person.push("删");
            if (pagePower[3] === "1") this.form.person.push("增");
            if (pagePower[4] === "1") this.form.log.push("查");
            if (pagePower[5] === "1") this.form.log.push("改");
            if (pagePower[6] === "1") this.form.log.push("删");
            if (pagePower[7] === "1") this.form.log.push("增");
            if (pagePower[8] === "1") this.form.manager.push("查");
            if (pagePower[9] === "1") this.form.manager.push("改");
            if (pagePower[10] === "1") this.form.manager.push("删");
            if (pagePower[11] === "1") this.form.manager.push("增");
            if (pagePower[12] === "1") this.form.role.push("查");
            if (pagePower[13] === "1") this.form.role.push("改");
            if (pagePower[14] === "1") this.form.role.push("删");
            if (pagePower[15] === "1") this.form.role.push("增");
            console.log(this.departmentList);
            for(let i=this.nmd.depPower.length-1,j=0;j<=this.departmentList.length;i--,j++){
                if (this.nmd.depPower[i] === "1") this.form.depPower.push(this.departmentList[j].label);
            }
            console.log(this.form);
        },
        //获取某角色下的管理员列表
        getList() {
            let here = this;
            axios({
                url: "/gllists",
                params: {
                    rid: here.nmd.rid
                }
            })
                .then(function(response) {
                    let data = response.data;
                    here.tableData = data;
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        goBack() {
            this.$router.go(-1);
        },
        handleDelete(index, row) {
            this.$confirm("是否移除该管理员?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                let here = this;
                axios({
                    url: "/gldel",
                    params: {
                        rid: here.nmd.rid,
                        gid: row.gid
                    }
                })
                    .then(function(response) {
                        if (response.data.success === 1) {
                            here.$message({
                                type: "success",
                                message: "移除成功!"
                            });
                            here.getList();
                        } else {
                            here.$message.error("移除失败!");
                        }
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            });
        },
        //给某管理员添加某角色
        submit() {
            let here = this;
            axios({
                url: "/gladd",
                params: {
                    gid: here.gid,
                    rid: here.nmd.rid
                }
            })
                .then(function(response) {
                    if (response.data.success === 1) {
                        here.$message({
                            message: "添加成功",
                            type: "success"
                        });
                    } else {
                        here.$message.error("添加失败!");
                    }
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        onSubmit() {
            let here = this;
            this.transformation();
            axios({
                method:"post",
                url: "/qxedit",
                data: here.qxForm
            })
                .then(function(response) {
                    if (response.data.success === 1) {
                        here.$message({
                            message: "修改成功",
                            type: "success"
                        });
                    } else {
                        here.$message.error("修改失败!");
                    }
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        reset() {
            this.init();
        },
        transformation() {
            console.log(this.form);
            this.qxForm.rid = this.form.rid;
            this.qxForm.name = this.form.name;
            this.qxForm.pagePower = "";
            this.qxForm.depPower = "";
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.person.indexOf("查")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.person.indexOf("改")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.person.indexOf("删")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.person.indexOf("增")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.log.indexOf("查")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.log.indexOf("改")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.log.indexOf("删")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.log.indexOf("增")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.manager.indexOf("查")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.manager.indexOf("改")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.manager.indexOf("删")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.manager.indexOf("增")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.role.indexOf("查")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.role.indexOf("改")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.role.indexOf("删")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat(this.form.role.indexOf("增")!==-1?"1":"0");
            this.qxForm.pagePower = this.qxForm.pagePower.concat("1111");
            this.qxForm.pagePower = this.qxForm.pagePower.split('').reverse().join('');
            this.qxForm.pagePower = parseInt(this.qxForm.pagePower, 2).toString(16);
            for(let i=this.departmentList.length-1;i>=0;i--){
                this.qxForm.depPower = this.qxForm.depPower.concat(this.form.depPower.indexOf(this.departmentList[i].label)!==-1?"1":"0");
            }
            //console.log(this.qxForm);
        }
    }
};
</script>

<style lang="stylus" scoped>
.main {
    background: #ffffff;
    padding: 20px;
}

.select, .el-input {
    width: 156px;
    margin-bottom: 10px;
}

.select {
    display: inline;
}

.el-page-header {
    margin-bottom: 10px;
}

.el-divider__text {
    font-size: 12px;
    color: #606266;
}
</style>


